import { Component, h, Host, Prop } from '@stencil/core';
import priceFormat from '../../global/functions/price';


@Component({
  tag: 'ks-cart-summary',
  styleUrl: 'cartSummary.css'
})
export class CartSummary {

    @Prop({reflect: true}) text: string;
    @Prop() price: number;
    @Prop({reflect: true}) large: boolean;
    @Prop({reflect: true}) emphasis: boolean;

    render() {
        const price = priceFormat(this.price);

        return <Host>
            <div class="name">{this.text}</div>
            <div class="price">{price}</div>
        </Host>;
    }
}